import GlobalComponent from '@/vueExtend/index'
//
import CommonFun from './common/CommonFun' //
console.log('plugin.js');

const MyPlugin={};
MyPlugin.install = function (Vue, options) {

    window.getJSONData=function(obj,num=10){
        var arr=[];
        for (let index = 1; index <= num ; index++) {
            var item={};
            for( let prop in obj){
                item[prop]=prop+""+index;
            }
            arr.push(item)
        }
        console.log(JSON.stringify(arr));
        return arr;
    }
    // getJSONData({name:"",age:""})
    
    window.getArrayData=function(str,num=10){
        var arr=[];
    
        for (let index = 1; index <= num ; index++) {
            
            arr.push(str+""+index);
        } 
        console.log(JSON.stringify(arr));
        return arr; 
    }
    // getArrayData('left')


    Vue.prototype.$message2 = GlobalComponent.Message2.install;
    Vue.prototype.$message1 = GlobalComponent.Message1.install;
    Vue.prototype.CommonFun = CommonFun //Vue



    //
    Vue.mixin({
        created: function () {
            //vue  window,;
            //name src +,vue,vue
            window['___' + this.$options.name] = this;
        }
    })


    //  `v-focus`
    Vue.directive('focus', {
        //  DOM 
        inserted: function (el) {
            // 
            el.focus()
        }
    })

    // 1. 
    Vue.myGlobalMethod = function () {
        // ...
    }

    // 2. 
    Vue.directive('my-directive', {
        bind(el, binding, vnode, oldVnode) {
            // ...
        }

    })

    // 3. 
    Vue.mixin({
        created: function () {
            // ...
        }

    })

    // 4. 
    Vue.prototype.$myMethod = function (methodOptions) {
        // ...
    }

    // 5. vue
    // console.log(require.context('./tygm/business'),true,/\.vue/);
    // 
    // let f=r=>{
    //     console.log(r.keys());
    //     return r.keys().map(key=>r(key));
    // }
    // console.log('',f(require.context('./tygm/business'),true,/\.vue/));
    window.menuArr=[];
    const requireComponents=require.context('./tygm/business',true,/\.vue/);
    requireComponents.keys().forEach(fileName=>{
        //
        const reqCom=requireComponents(fileName)
        console.log(fileName);
        //
        var arr=fileName.split('/');
        const reqComName=arr[arr.length-1].replace('.vue','')
        menuArr.push(reqComName)
        //,
        Vue.component(reqComName,reqCom.default||reqCom)
    })

    //6.

    Vue.config.optionMergeStrategies._my_option = function (parent, child, vm) {
        if(child){
            return child + 1
        }else{
            return '0'
        }
      }
    //   7.  Vue  Vue  (e.g.  Web Components APIs) Unknown custom element 
      Vue.config.ignoredElements = [
        'my-custom-web-component',
        'another-web-component',
        //  `RegExp` ion-
        //  2.5+ 
        /^ion-/
      ]
    //   8.
    //   <input type="text" @keyup.media-play-pause="method">
    //    v-on 
      Vue.config.keyCodes = {
        a: 65,
        f1: 112,
        // camelCase 
        mediaPlayPause: 179,
        //  kebab-case 
        "media-play-pause": 179,
        up: [38, 87],
        
      }

      // ctrl+z  ___APP
      document.onkeydown=function(event){
        if(event.keyCode=='90'&&event.ctrlKey==true){
            ___APP.$router.go(-1)
         }
    }
}
export default MyPlugin