import CommonFun from './common/CommonFun' //

const MyPlugin = {};
const EventUtil = CommonFun.EventUtil;

MyPlugin.install = function (Vue, options) {
    let vuePro = Vue.prototype;
    // 2. 
    Vue.directive('entertwice', {
        bind(el, binding, vnode, oldVnode) {
            let firstvalue = "";
            let lastValue = null; //
            EventUtil.addHandler(el, 'blur', function (event) {
                if (this.value != '' || firstvalue != '') {
                    console.log('entertwice  ');
                    // ___APP.$message('entertwice  ');
                    //,,,
                    if (lastValue != this.value) {
                        lastValue = null; //,null
                        if (firstvalue == this.value && firstvalue != '') {
                            //
                            firstvalue = "";
                            lastValue = this.value;
                            // $target.trigger('twiceDone');//

                        } else {
                            if (firstvalue == '') {
                                firstvalue = this.value;
                                // ___APP.$message('');
                                vuePro.$message('');

                            } else {
                                vuePro.$message(',');
                                firstvalue = this.value;
                            }

                        }
                    }
                }
            })
        }

    })

}
export default MyPlugin