import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);
const state = { //state
  showNav: true,
  showFooter: true,
  changableNum: 0
  //
};
const getters = { //state()
  isNavShow(state) { //showFooter
    console.log(' state.showNav');
    return state.showNav
  },
  isShow(state) { //showFooter
    return state.showFooter
  },
  getChangedNum() { //changebleNum
    return state.changableNum
  }
};
const mutations = {
  showNav(state) { //statestate();
    state.showNav = true;
  },
  hideNav(state) { //
    state.showNav = false;
  },
  show(state) { //statestate();
    state.showFooter = true;
  },
  hide(state) { //
    state.showFooter = false;
  },
  newNum(state, sum) { //statesum
    state.changableNum += sum;
  }
};
// this.$store.dispatch('showNav');
const actions = {
  hideNav(context) { //mutationscontextstore 
    context.commit('hideNav');
  },
  showNav(context) { //
    context.commit('showNav');
  },
  hideFooter(context) { //mutationscontextstore 
    context.commit('hide');
  },
  showFooter(context) { //
    context.commit('show');
  },
  getNewNum(context, num) { //num
    context.commit('newNum', num);
  }
};
const store = new Vuex.Store({
  state,
  getters,
  mutations,
  actions
});
export default store;