      <legend>v-model </legend>
      <div class="fieldContent">
        <p>{{name}}</p>
        <input type="text" v-model="name">
      </div>
    </fieldset>
    <fieldset class="fieldset">
      <legend>Object.freeze() __</legend>
      <div class="fieldContent">
        <p>{{person.name}}</p>
        <input type="text" v-model="person.name">
