    </fieldset>
    <fieldset class="fieldset">
      <legend>v-once </legend>
      <div class="fieldContent">
        <span v-once>: {{ once }}</span>
        <p>{{once}}</p>
        <input type="text" v-model="once">
      </div>
    </fieldset>
    <fieldset class="fieldset">
      <legend> HTML</legend>
      <div class="fieldContent">
        <p>Using mustaches: {{ rawHtml }}</p>
        <p>Using v-html directive:
          <span v-html="rawHtml"></span>
        </p>
      </div>
    </fieldset>
    <fieldset class="fieldset">
      <legend></legend>
      <div class="fieldContent">
        <p>v-on:submit.prevent="onSubmit"</p>
        <form v-on:submit.prevent="onSubmit">...
          <button type="submit">1</button>
        </form>
        <p>v-on:submit="onSubmit//"</p>
        <form v-on:submit="onSubmit">...
          <button type="submit">2</button>
        </form>
      </div>
    </fieldset>
    <fieldset class="fieldset">
      <legend> vs </legend>
      <div class="fieldContent">
        <p></p>
        <p>
          Reversed message:{{computedReversedMessage}}
        </p>
        <p></p>
        <p>Reversed message:{{reversedMessage()}}</p>
        <p> A A  A  getter</p>
      </div>
    </fieldset>
    <fieldset class="fieldset">
      <legend> </legend>
      <div class="fieldContent">
        <p>
          fullName:{{fullName}}
        </p>
      </div>
    </fieldset>
    <fieldset class="fieldset">
      <legend> gettersetter</legend>
      <div class="fieldContent">
        <p>
          computedfullName:{{computedfullName}}
        </p>
      </div>
    </fieldset>
    <fieldset class="fieldset">
      <legend> Class </legend>
      <div class="fieldContent">
        <div>
          <button type="button" @click="activeHandle">active</button>
          <button @click="errorHandle" type="button">error</button>
          <button type="button" @click="noactiveHandle">noactive</button>
          <button @click="noerrorHandle" type="button">noerror</button>
        </div>
        <p>
          classObject class
          <div v-bind:class="classObject"></div>classObject: {{JSON.stringify(classObject)}}
        </p>
      </div>
    </fieldset>
    <fieldset class="fieldset">
      <legend> v-if </legend>
      <div class="fieldContent">
        <textarea>
          
          <template>  v-if   v-if 
            <template>  v-if
              <template> 
        </textarea>
      </div>
    </fieldset>
    <fieldset class="fieldset">
      <legend>  </legend>
      <div class="fieldContent">
        <p v-for="item in arr">
          {{item}}
        </p>
        <p>Vue  push() pop() shift() unshift() splice() sort() reverse()
        </p>
        <p>
           vm.items[indexOfItem] = newValue 
          <p>
            // Vue.set Vue.set(vm.items, indexOfItem, newValue) // Array.prototype.splice vm.items.splice(indexOfItem, 1, newValue)
          </p>

        </p>
        <p>:window.viewsHome.$set(window.viewsHome.arr,0,'a1')</p>
        <p>:window.viewsHome.arr.splice(0,0,'a0')</p>
        <textarea style="display:block;">
            (mutation method) (non-mutating method) filter(), concat()  slice() 
          example1.items = example1.items.filter(function (item) { return item.message.match(/Foo/) })
        </textarea>
      </div>
    </fieldset>
    <fieldset class="fieldset">
      <legend> v-on:click.prevent.self  v-on:click.self.prevent</legend>
      <div class="fieldContent">
        <p> v-on:click.prevent.self  v-on:click.self.prevent </p>
        <p>: ,, </p>
        <p>click.prevent.self:,</p>
        <p>click.self.prevent:,click,,,,click,,a,,</p>
      </div>
    </fieldset>
  </div>
  </template>

  <script>
    // @ is an alias to /src
    // import HelloWorld from '@/components/HelloWorld.vue'
    const person = {
      name: ''
    };
    Object.freeze(person);
    export default {
      name: 'home',
      data() {
        return {
          name: '',
          once: 'once',
          person: person,
          rawHtml: '<span style="color: red">This should be red.</span>',
          message: 'Hello',
          firstName: 'Foo',
          lastName: 'Bar',
          fullName: 'Foo Bar',
          //class 
          isActive: true,
          error: null,
          arr: ['a', 'b', 'c'],
        }
      },
      computed: {
        //class 
        classObject: function () {
          return {
            active: this.isActive && !this.error,
            'text-danger': this.error && this.error.type === 'fatal'
          }
        },
        //  getter
        computedReversedMessage: function () {
          // `this`  vm 
          return this.message.split('').reverse().join('')
        },
        computedfullName: {
          // getter
          get: function () {
            return this.firstName + ' ' + this.lastName
          },
          // setter
          set: function (newValue) {
            var names = newValue.split(' ')
            this.firstName = names[0]
            this.lastName = names[names.length - 1]
          }
        },
      },
      watch: {
        firstName: function (val) {
          this.fullName = val + ' ' + this.lastName
        },
        lastName: function (val) {
          this.fullName = this.firstName + ' ' + val
        }
      },
      created: function () {
      },
      methods: {
        click1:function(){
          alert('v-on:click.prevent.self')
        },
        click2:function(){
          alert('v-on:click.self.prevent')
        },
        click3:function(){
          alert('click3')
        },
        activeHandle: function () {
          this.active = true;
        },
        errorHandle: function () {
          this.error = true;
        },
        noactiveHandle: function () {
          this.active = false;
        },
        noerrorHandle: function () {
          this.error = false;
        },
        onSubmit: function () {
          console.log('onSubmit');
        },
        reversedMessage: function () {
          return this.message.split('').reverse().join('')
        },
      },
      components: {
        // HelloWorld

      }
    }
  </script>
